<template>
    <div>
       <div class="search"><input v-model="keyword" class="input" placeholder="输入城市或拼音" /></div>
        <div class="search-content" ref="search" v-show="keyword">
            <ul>
            	<li class="li" v-for="item of list" :key="item.id" @click="listenCity(item.name)">{{item.name}}</li>
            	<li class="li" v-show="!list.length">没有找到</li>
            </ul>
        </div>
        
    </div>
</template>

<script>
    import Bscroll from 'better-scroll'
export default {
    name: 'CityHeaderSearch',
    props:{
      cities:Object  
    },
    mounted(){
        const options = {
                click: true,            //点击事件
                taps: true
                }
      this.scroll = new Bscroll(this.$refs.search,options)   //加载Bscroll
    },
    methods:{
        listenCity(city){
            this.$store.commit('changCity',city)       //数据共享 选哪个城市 本地就变成哪个  dispatch()派发一个actions 给store  
            this.$router.push('/')    //实现页面的JS跳转
        }
    },
    data(){
        return{
            keyword:'',
            list:[],
            timer:null
        }
    },
    watch:{
        keyword(){
           if(this.timer){
                clearTimeout(this.timer)
            }
            if(!this.keyword){  //判断
                this.list = []
                return
            }
            this.timer = setTimeout(() => {
                const result =[];
                for(let i in this.cities){
                    this.cities[i].forEach((value)=>{
                       //console.log(value)
                       if(value.spell.indexOf(this.keyword)>-1 || value.name.indexOf(this.keyword) > -1){
                           result.push(value)
                       }
                    })
                }
                this.list = result
            },100) 
           
        }
    }
}
</script>

<style scoped="scoped">

.search {
    height: 2.5rem;
    background-color: #00bcd4;
    padding: 0 0.6rem;
    line-height: 1.875rem;
}
.input {
    box-sizing: border-box;
    color: #666;
    text-align: center;
    padding: 0 0.1rem;
    height: 1.875rem;
    width: 100%;
    border-radius: 0.3rem;
    border: 0.0625rem solid #DBDBDB;
}
.search-content{
    position: absolute;
    top: 7.125rem;
    right: 0;left: 0;bottom: 0;
    overflow: hidden;
    z-index: 1;
    padding-top: .2rem;
    background-color: #ddd;
}
.li{
    font-size: 0.9375rem;
    height: 2rem;
    line-height: 2rem;
    border-bottom: 0.0625rem solid #ccc;
    padding-left: 0.9rem;
    color: #666;
  background-color: #fff;
    
}
</style>



