<template>
    <div>
        <div class="header">
            <div class="back"><span class="iconfont icon-fanhui"></span></div>
            
                <div class="input">
                    <router-link to="search">
                        <span class="iconfont icon-sousuo"></span>
                        <span class="aa">输入城市/景点/游玩主题</span>
                    </router-link>
                </div>
             
            <router-link to="city">
                <div class="city">
                    {{this.$store.state.city}}
                    <span class="iconfont icon-jiantou"></span>
                </div>
            </router-link>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Header',
    props:{
        city:String
    }
};
</script>

<style scoped>
    .aa{
        color: #999;
    }
.header {
    display: flex;
    flex-grow: 1;
    width: 100%;
    background-color: #00bcd4;
    height: 2.75rem;
    line-height: 2.75rem;
}
.back {
    text-align: center;
    width:2.5rem;
    color:#fff;
    font-weight: bolder;
}
.icon-fanhui{
    font-size: 1.125rem;
}
.input {
    background-color: #fff;
    height: 1.875rem;
    width: 15.3125rem;
    line-height: 1.875rem;
    margin-top: 0.45rem;
    border-radius: 3px;
    padding-left: .5rem;
    color: #bbb;
    font-size: 0.9rem;
    flex-grow: 1;
}
.city {
    text-align: center;
    min-width: 4rem;
    padding: 0 0.125rem;
    color:#fff;
    font-size: .85rem;
}
.icon-jiantou{
    font-size: .8rem;
}
</style>



