<template>
    <div>
        <div class="title">
            <sapn class="xin">❤</sapn>
            猜你喜欢
        </div>
        <ul>
            <router-link :to="'/details/'+item.id" tag="li" class="content" v-for="item of list" :key="item.id">
                <div class="img-wrap">
                    <img
                        :src="item.likeImgUrl"
                    />
                </div>
                <div class="text">
                    <div class="text-title">{{item.likeTitle}}</div>
                    <div class="text-hot">
                        <span class="start">❤❤❤❤</span>
                        {{item.comment}}条评论
                    </div>
                    <div class="text-price">
                        <span class="fuh">￥</span><span class="num">{{item.likePrice}}</span>
                        起
                    </div>
                </div>
                <span class="area">{{item.area}}</span>
            </router-link>
        </ul>
    </div>
</template>

<script>
export default {
    name: 'GuessYouLike',
    props:{
        list:Array
    }
}
</script>

<style scoped>
.xin {
    color: red;
}
.title {
    height: 2.9375rem;
    line-height: 2.9375rem;
    margin-left: 0.7rem;
    color: #666;
}
.content {
    width: 100%;
    height: 8rem;
    position: relative;
    border-bottom: 0.0625rem solid #eee;
}
.img-wrap {
    float: left;
    width: 6.5rem;
    height: 6.5rem;
    padding-top: 0.625rem;
    margin-left: .7rem;
}
img {
    width: 100%;
    height: 100%;
    display: block;
}
.text {
    overflow: hidden;
    float: left;
    margin-left: 0.3125rem;
    margin-top: 0.28rem;
}
.text > div {
    margin-top: 0.75rem;
}
.text-title {
    font-size: 1.125rem;
    color: #333;
}
.area {
    float: right;
    position: absolute;
    bottom: 1.15rem;
    right: 1.25rem;
    font-size: 0.875rem;
    color: #aaa;
}
.text-hot {
    color: #ccc;
    font-size: 0.875rem;
}
.start {
    margin-left: .7rem;
    margin-right: 1rem;
    color: #ffb436;
}
.text-price {
    margin-left: .4rem;
}
.fuh {
    color: #ff8300;
    font-size: 0.875rem;
}
.num {
    font-size: 1.5rem;
    color: #ff8300;
}
</style>



