<template>
    <div class="list" ref="wrapper">
        <div>
            <div class="area">
                <div class="title">当前位置</div>
                <div class="btn-list">
                    <div class="btn-wrap">
                        <div class="btn">
                            {{this.$store.state.city}}
                        </div>
                    </div>
                </div>    
            </div>
            <div class="area">
                <div class="title">热门城市</div>
                <div class="btn-list">
                    <div class="btn-wrap" v-for="item of hotcities" :key="item.id" @click="listenCity(item.name)">
                        <div class="btn">
                            {{item.name}}
                        </div>
                    </div>
                </div>     
            </div>
            <div class="area" v-for="(item,key) of cities" :key="key" :ref="key">
                <div class="title">{{key}}</div>
                <div class="item-list">
                    <div class="item" v-for="innerItem of item" :key="innerItem.id" @click="listenCity(innerItem.name)">
                        <div class="p">
                            {{innerItem.name}}
                        </div>
                    </div>
                </div>     
            </div>
        </div>
    </div>
</template>

<script>
import Bscroll from 'better-scroll';  //引入Bscroll
export default {
    name: 'CityList',
    props: {
        cities: Object,
        hotcities: Array,
        letter:String
    },
    methods:{
        listenCity(city){
            this.$store.commit('changCity',city)       //数据共享 选哪个城市 本地就变成哪个  dispatch()派发一个actions 给store  
            this.$router.push('/')    //实现页面的JS跳转
        }
    },
    watch:{
        letter(){
            if(this.letter){
                const ele = this.$refs[this.letter][0]
                this.scroll.scrollToElement(ele)  //scroll 滚动到特定区域
            }
        }
    },
    mounted() {
        const options = {
                click: true,            //点击事件
                taps: true
                }
                this.scroll = new Bscroll(this.$refs.wrapper,options)   //加载Bscroll
    }
}
</script>

<style scoped>
.list{
    overflow: hidden;
        position: absolute;
        top: 7.125rem;
        left: 0;
        bottom: 0;
        right: 0;
    }
.area{
    
}
.title{
    height: 2rem;
    line-height: 2rem;
    background-color: #eee;
    font-size: 0.9375rem;
    padding-left: 0.625rem;
}
.btn-list{
    overflow: hidden;
    padding: 0.225rem;
    padding-right: 1.5625rem;
}
.btn-wrap{
    float: left;
    width: 33.33%;
}
.btn{
    margin: 0.525rem;
    padding: 0.325rem 0;
    font-size: 0.875rem;
    text-align: center;
    border: 0.0625rem solid #ccc;
    box-sizing: border-box;
    border-radius: 0.2rem;
}

.item-list{
    padding-left: 0.625rem;
    
    line-height: 2rem;
}
</style>



