<template>
<div class="wrapper" ref="wrapper">
        <div>
            <home-header :city="city"></home-header>
            <swiper :swiperList="swiperList"></swiper>
            <icons :list="iconsImg"></icons>
            <hot-week :hotWeekList="hotWeek"></hot-week>
            <guess-you-like :list="likeList"></guess-you-like>
            <week-go :weekList="weekList"></week-go>
            <home-footer></home-footer>
        </div>
</div>
</template>

<script>
    import Bscroll from 'better-scroll'
import header from './home/Header.vue'
import swiper from './home/Swiper.vue'
import icons from './home/Icons.vue'
import GuessYouLike from './home/guessYouLike.vue'
import WeekGo from './home/weekGo'
import HomeFooter from './home/footer'
import HotWeek from './home/hotweek'
import axios from 'axios'   //引入ajax   先在npm中安装
export default{
    name:'Home',
    data () {
      return {
          city:'',
          swiperList:[],
          iconsImg:[],
          likeList:[],
          weekList:[],
          hotWeek:[]
      }  
    },
    components:{
        HomeHeader:header,
        swiper,
        icons,
        GuessYouLike,
        WeekGo,
        HomeFooter,
        HotWeek,
    },
    methods:{
       getHomeData(){
           axios.get('./api/index.json')
           .then(this.getHomeDataSucc)
       },
       getHomeDataSucc(re){
           re = re.data
           if(re.ret && re.data){
               const data = re.data
               this.city = data.city
               this.swiperList = data.swiperList
               this.iconsImg = data.iconsImg
               this.likeList = data.likeList
               this.weekList = data.weekList
               this.hotWeek = data.hotWeek
           }
       }
    },
    mounted (){              //利用生命周期函数 写 ajax    前提先引入 axios   json数据交互的第一步
        this.getHomeData()   //页面加载好后执行函数
        const options = {
                click: true,
                taps: true
                }
        this.scroll = new Bscroll(this.$refs.wrapper,options)
    }
}
</script>

<style scoped>
    .wrapper{
        position: absolute;
        top: 0;right: 0;left: 0;bottom: 0;
    }
</style>



