<template>
	<div>
        <div class="header">
            <router-link to="/">
                <div class="back"><span class="iconfont icon-fanhui"></span></div>
            </router-link>
                <input v-model="keyword" class="input-item" type="text" placeholder="请输入景区或景点">
            <router-link to="city">
                <div class="city">
                    搜索
                </div>
            </router-link>
        </div>
        <div class="search" ref="search">
            <ul>
            	<li class="li" v-for="item of list" :key="item.id" @click="listenCity(item.name)">{{item.name}}</li>
            	<li class="li" v-show="!keyword.length">没有找到</li>
            </ul>
        </div>
    </div>
</template>

<script>
    import Bscroll from 'better-scroll'
    import axios from 'axios'   //引入ajax   先在npm中安装
    export default{
        name:'homeSearch',
        data(){
            return{
                cities:{},
                keyword:'',
                list:[],
                timer:null
            }
        },
        watch:{
          keyword(){
              if(this.timer){
                   clearTimeout(this.timer)
               }
               if(!this.keyword){  //判断
                   this.list = []
                   return
               }
               this.timer = setTimeout(() => {
                   const result =[];
                   for(let i in this.cities){
                       this.cities[i].forEach((value)=>{
                          //console.log(value)
                          if(value.spell.indexOf(this.keyword)>-1 || value.name.indexOf(this.keyword) > -1){
                              result.push(value)
                          }
                       })
                   }
                   this.list = result
               },100) 
          }  
        },
        methods:{
            getData(){
                axios.get('/api/city.json').then(this.getDataSucc)
            },
            getDataSucc(re){
                re = re.data;
                if(re.ret && re.data){
                    const data = re.data
                    this.cities = data.cities
                }
            },
            listenCity(city){
                this.$store.commit('changCity',city)       //数据共享 选哪个城市 本地就变成哪个  dispatch()派发一个actions 给store  
                this.$router.push('/')    //实现页面的JS跳转
            }
        },
        mounted() {
        	this.getData();
            const options = {
                    click: true,            //点击事件
                    taps: true
                    }
            this.scroll = new Bscroll(this.$refs.search,options)   //加载Bscroll
        }
    }
</script>

<style scoped="scoped">
    .header {
        display: flex;
        flex-grow: 1;
        width: 100%;
        background-color: #fff;
        height: 2.75rem;
        line-height: 2.75rem;
    }
    .back {
        text-align: center;
        width:2.5rem;
        color:#000;
        font-weight: bolder;
    }
    .icon-fanhui{
        font-size: 1.125rem;
    }
    .input {
        
    }
    .input-item{
        text-align: center;
        background-color: #eee;
        height: 1.875rem;
        width: 15.3125rem;
        line-height: 1.875rem;
        margin-top: 0.45rem;
        border-radius: .9rem;
        padding-left: 0.8rem;
        color: #bbb;
        font-size: 0.9rem;
        flex-grow: 1;
        box-sizing: border-box;
        border: 0.0625rem solid #fff;
    }
    .city {
        text-align: center;
        width: 4rem;
        color:#000;
        font-size: .85rem;
    }
    .icon-jiantou{
        font-size: .8rem;
    }
    .search{
       position: absolute;
       top: 2.75rem;
       right: 0;left: 0;bottom: 0;
    }
    .li{
        background-color: #eee;
        border-bottom: 0.0625rem solid #ccc;
        padding-left: 1rem;
        height: 2rem;
        line-height: 2rem;
    }
</style>



