<template>
    <div>
        <city-header></city-header>
        <city-header-search :cities="cities"></city-header-search>
        <city-list :cities="cities" :hotcities="hotcities" :letter="letter"></city-list>
        <city-alphabet :cities="cities" @change="letterChange"></city-alphabet>
    </div>
</template>

<script>
import axios from 'axios'
import CityHeader from './components/header'
import CityHeaderSearch from './components/headerSearch'
import CityList from './components/list'
import CityAlphabet from './components/alphabet'

export default{
    name:'City',
    components:{
        CityHeader,
        CityHeaderSearch,
        CityList,
        CityAlphabet,
    },
    data(){
        return{
            cities:{},
            hotcities:[],
            letter:''
        }
    },
    methods:{
        getCityData(){
            axios.get('/api/city.json')
            .then(this.getCityDataSucc)
        },
        getCityDataSucc(re){
            re = re.data
            const data = re.data
            this.cities = data.cities
            this.hotcities = data.hotCities
        },
        letterChange(letter){
            this.letter = letter
        }
    },
    mounted() {
    	this.getCityData()
    }
}
</script>

<style></style>



