<template>
    <div>
        <div class="title">
            周末去哪
        </div>
        <div class="week-go" v-for="item of weekList" :key="item.id">
            <div class="img">
                <img :src="item.weekImgUrl" :alt="item.weekTitle" class="img-content">
            </div>
            <div class="text">
                <p class="p-tit">{{item.weekTitle}}</p>
                <p class="p-con">{{item.weekContent}}</p>
            </div>
        </div>
        
        
    </div>
</template>

<script>
export default {
    name: 'WeekGo',
    props:{
        weekList:Array
    }
}
</script>

<style scoped>
    .title{
        background-color: #eee;
        padding-left: 0.8rem;
        height: 2rem;
        font-size: 0.9375rem;
        color: #333;
        line-height: 2rem;
    }
    .img{
        width: 100%;
        height: 8.4375rem;
    }
    .img-content{
        width: 100%;
        height: 100%;
    }
    .text{
        padding: 0.625rem;
        
    }
    .p-tit,.p-con{
        padding: 0.1875rem;
    }
    .p-tit{
        color: #222;
        font-size: 0.875rem;
    }
    .p-con{
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        color: #888;
        font-size: 0.75rem;
        padding-right: 2.5rem;
    }
</style>



