<template>
	<div class="list">
        <ul>
        	<li v-for="(item,key) of cities" :key="key" @click="handleData" @touchStart="handTouchStart" @touchMove="handTouchStart" @touchEnd="handTouchEnd">{{key}}</li>
        </ul>
    </div>
</template>

<script>
    export default{
        name:'CityAlphabet',
        props:{
            cities:Object
        },
        methods: {
        	handleData(e){
                this.$emit("change", e.target.innerText)
            },
            handTouchStart(){
                
            },
            handTouchMove(){
                
            },
            handTouchEnd(){
                
            }
        }
    }
</script>

<style scoped>
    .list{
        color: #007AFF;
        position: absolute;
        text-align: center;
        right: 0;top: 7.125rem;bottom: 0;
        width: 1.5625rem;
        background-color: #fff;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
</style>



