<template>
    <div>
        <div class="header">
            <router-link to="./"><span class="iconfont icon-fanhui"></span></router-link>
            城市选择
        </div>
        <div class="header-choose">
            <div class="ul">
                <div class="li a active">境内</div>
                <div class="li">境外·港澳台</div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'CityHeader'
};
</script>

<style scoped="scoped">
.icon-fanhui {
    color: #fff;
    font-weight: 900;
    font-size: 1.125rem;
    width: 2.5rem;
    text-align: center;
    position: absolute;
    top: 0;
    left: 0.45rem;
    right: 0;
    bottom: 0;
}
.header {
    position: relative;
    font-size: 1rem;
    height: 2.125rem;
    line-height: 2.9rem;
    color: #fff;
    text-align: center;
    background-color: #00bcd4;
}
.header-choose {
    background-color: #00bcd4;
    height: 2.1rem;
    padding: .2rem 2.5rem;
}
.ul{
    margin-top: .25rem;
    font-size: 0.875rem;
    height: 1.375rem;
    display: flex;
    flex-wrap: wrap;
    border: 0.0625rem solid #fff;
    border-radius: .2rem;
}
.a{
    border-right: .05rem solid #fff;
}
.li{
    line-height: 1.375rem;
    flex-grow: 1;
    text-align: center;
    color:#fff;
}
.active{
    background-color: #fff;
    color: #00bcd4;
}
</style>



