<template>
    <div class="wrap">
        <swiper :options="swiperOption" v-if="showSwiper">  <!-- 为了初始swiper显示的是第一页-->
            <!-- slides -->
            <swiper-slide v-for="item of swiperList" :key="item.id">
                <img class="swiper-img" :src="item.swiperImg" />
            </swiper-slide>
            <!-- <swiper-slide><img class="swiper-img" src="../../assets/images/2.jpg" /></swiper-slide>
            <swiper-slide><img class="swiper-img" src="../../assets/images/3.jpg" /></swiper-slide>
            <swiper-slide><img class="swiper-img" src="../../assets/images/4.jpg" /></swiper-slide> -->
            <!-- Optional controls -->
            <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
    </div>
</template>

<script>
export default {
    name: 'HomeSwiper',
    props:{
        swiperList: Array
    },
    data() {
        return {
            swiperOption: {
                pagination: '.swiper-pagination',
                loop: true,
                
            },
            
            /* swiperList: [
                {
                    id: '0001',
                    swiperImg:
                        'http://mp-piao-admincp.qunarzz.com/mp_piao_admin_mp_piao_admin/admin/20192/c8273823e3d561357875ed21a698d918.png_750x200_deb12517.png'
                },
                {
                    id: '0002',
                    swiperImg:
                        'http://img1.qunarzz.com/piao/fusion/1811/31/da037478f37cf202.jpg_750x200_fe28d396.jpg'
                },
                {
                    id: '0003',
                    swiperImg:
                        'http://img1.qunarzz.com/piao/fusion/1812/d1/cd4f32e924a5a302.jpg_750x200_3d492837.jpg'
                },
                {
                    id: '0004',
                    swiperImg:
                        'http://img1.qunarzz.com/piao/fusion/1812/80/c84c17a6b31a0002.jpg_750x200_0f5d0a63.jpg'
                }
            ] */
        };
    },
    computed :{
        showSwiper(){
            return this.swiperList.length
        }
    }
};
</script>

<style scoped>
.wrap {
    width: 100%;
    height: 27vw;
}
.swiper-img {
    width: 100%;
}
</style>



