<template>
	<div>
        <div class="title">
            <span class="star">❤</span>本周热门榜单
        </div>
        <ul>
        	<li class="wrap" v-for="item of hotWeekList" :key="item.id">
                    <div class="img">
                        <img :src="item.hotWeekImgUrl" :alt="item.hotWeekTitle">
                    </div>
                    <div class="wrap-title">
                        <p>{{item.hotWeekTitle}}</p>
                        <p><span>￥{{item.hotWeekMoney}}</span>起</p>
                    </div>
            </li>
            
        </ul>
        <div class="bottom"></div>
    </div>
</template>

<script>
    export default{
        name:'hotWeek',
        props:{
            hotWeekList:Array
        }
    }
</script>

<style scoped>
    .star{
        color: red;
    }
    .title{
        padding: 0.6875rem 0 0.6875rem 0.7rem;
        font-size: 0.9375rem;
        color: #666;
    }
    .img{
        width: 6.25rem;
        height: 6.25rem;
    }
    ul{
        padding: 0.2rem 0.625rem;
            overflow-x: scroll;
    white-space: nowrap;
    }
    .wrap{
        padding: 0.125rem;
        display: inline-block;
    }
    img{
        width: 100%;
        height: 100%;
    }
    p{
        text-align: center;
        color: #333;
        font-size: 0.8125rem;
    }
    .wrap-title > p{
        padding: 0.3125rem;
    }
    .wrap-title > p > span{
        color: #ff8300;
    }
    .wrap-title > p:nth-child(1){
        width: 6.1875rem;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
    }
    .bottom{
        height: 0.3125rem;
        background-color: #eee;
    }
</style>



