<template>
    <div>
        <swiper :options="swiperOption">
            <swiper-slide v-for="(page, index) of pages" :key="index">
                <div class="icon-wrap">
                    <div class="icons" v-for="item of page" :key="item.id">
                        <div class="icon-img"><img :src="item.iconsImgUrl" /></div>
                        <p class="text-p">{{ item.text }}</p>
                    </div>
                </div>
            </swiper-slide>
            <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
        <div class="bottom-info">
            <p class="bottom-left">定位失败</p>
            <p class="bottom-right">必游榜单</p>
        </div>
    </div>
</template>
<script>
export default {
    name: 'Icons',
    props:{
        list: Array
    },
    data() {
        return {
            swiperOption: {
                pagination: '.swiper-pagination',
                loop: false
            },
        }
    },
    computed: {
        pages() {
            //计算属性  使得swiper自动计算是否有第二页  还需要改动 swiper-slide 看上面
            const pages = [];
            this.list.forEach((item, index) => {
                const page = Math.floor(index / 8);
                if (!pages[page]) {
                    pages[page] = [];
                }
                pages[page].push(item);
            });
            return pages;
        }
    }
};
</script>
<style scoped>
.icon-wrap {
    width: 100%;
    height: 0;
    padding-bottom: 50%;
    border-bottom: 0.0625rem solid #eee;
}
.icons {
    float: left;
    width: 25%;
    height: 0;
    padding-bottom: 22%;
    position: relative;
}
.icon-img {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 1rem;
    padding: 0.5rem;
}
img {
    width: 75%;
    display: block;
    margin: 0 auto;
}
.text-p {
    text-align: center;
    position: absolute;
    right: 0;
    left: 0;
    bottom: 0;
    height: 1.2rem;
    line-height: 1.2rem;
    font-size: 0.875rem;
    color: #333;
}
.swiper-pagination-bullet-active {
    background: black !important;
}
.bottom-info {
    text-align: center;
    width: 100%;
    height: 3.125rem;
    line-height: 3.125rem;
    display: flex;
    flex-wrap: wrap;
    border-bottom: 0.0625rem solid #eee;
}
.bottom-left {
    flex-grow: 1;
    border-right: 0.0625rem solid #eee;
}
.bottom-right {
    flex-grow: 1;
}
</style>
<style></style>



