</script>

<style scoped="scoped">
    .wrap{
        background-color: #f3f3f3;
        }
    .list-wrap{
        padding: 0.8rem;
        box-sizing: border-box;
        border-bottom: 0.05rem solid #ccc;
    }
    .list-a{
        color: #888;
        font-size: 0.875rem;
    }
    .list-b{
        color: #25a4bb;
        font-size: 0.9375rem;
    }
    .list-a,.list-b{
        text-align: center;
        padding: 0.3125rem;
    }
        span{
            margin-left: 0.25rem;
        }
        
        .bottom-wrap,.beian{
            text-align: center;
            font-size: 0.875rem;
        }
        .bottom-wrap > span:nth-child(2){
