import Vue from 'vue'
import Vuex from 'vuex'
import state from './state'

Vue.use(Vuex)                 //创建VUEX组件   先在 npm install vuex --save


export default new Vuex.Store({
    state,              //可以缩减成一个文件  然后import进来就行   state:state,缩减成 state,
    mutations:{
        changCity(state,city){
            state.city = city
            try{
                localStorage.city = city  //吧city储存在本地  类似cookies  使得页面刷新后city还是你选择的城市
            }catch(e){}
        }
    }
})




